<template>
  <div class="event" ref="msgElement">

    <member :chat="chat" :event="event"
            :userinfo="userinfo"
            :readed="readed"
            :preview="preview || false"
            v-if="type === 'member'"/>

    <message @openGalleryImg="openImage" :chat="chat"
             :event="event._clearEvent"
             :origin="event" :senderId="event.sender.userId"
             :imgEvent="galleryData" :userinfo="userinfo"
             :filePreview="filePreview" :baseImg="prevImg" :readed="readed"
             :preview="preview || false"
             :clientWidth="clientWidth"
             :encrypted="encrypted"
             v-if="type === 'message'" @updatedSize="updatedSize"/>

    <common :event="event._clearEvent"
            :userinfo="userinfo"
            :readed="readed"
            :preview="preview || false"
            v-if="type === 'common'"/>
  </div>
</template>

<script>
import common from '@/components/events/event/common/index.vue'
import member from '@/components/events/event/member/index.vue'
import message from '@/components/events/event/message/index.vue'

export default {
  name: 'event',

  components: {
    common,
    member,
    message,
  },

  data: function () {
    return {
      readed: false,
    }
  },


  props: {
    event: Object,
    preview: Boolean,
    prevImg: {
      type: String,
      default: 'empty'
    },
    chat: Object,
    filePreview: {
      type: Object,
      default: function () {
        return {
          show: false
        }
      }
    },
    metaUrl: String,
    galleryData: {},
    goToGallery: Function,
    clientWidth: Number
  },

  computed: {
    type: function () {

      console.log('this.event', this.event)
      let ev = this.event
      var event = this.event.event
      var t = event.type
      if (['m.room.member'].indexOf(t) > -1) {
        return 'member'
      }

      if (['m.room.encrypted'].indexOf(t) > -1) return 'message'
      if (['m.room.message'].indexOf(t) > -1) {
        return 'message'
      }
      if (['m.room.name'].indexOf(t) > -1) return 'member'
      if (['m.room.power_levels'].indexOf(t) > -1) return 'member'

      return ''

    },

    userinfo: function () {

      return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(this.event.event.sender)) || {}

    },
    encrypted: function () {
      if (this.chat && this.chat.roomId) {
        return this.core.mtrx.client.isRoomEncrypted(this.chat.roomId)
      }

      return false

    },


  },
  mounted: function () {
    // console.log('this.event.event', this.event.event)
    this.checkReaded()
  },

  methods: {
    updatedSize: function (before) {
      this.$emit('updatedSize', before)
    },
    checkReaded: function () {
      if (this.event) {
        this.core.mtrx.isReaded(this.event).then(readed => {
          this.readed = !!readed
        })
      }
    },
    openImage: function () {
      this.$emit('openImageEvent', this.event)
    },

    removeEvent: function () {
      this.$emit('removeEvent')
    },
  }
}
</script>

