import {mapState} from 'vuex';
import f from "@/application/functions";
import _ from 'underscore'

export default {
  name: 'eventsMember',
  props: {
    event: Object,
    preview: Boolean,
    userinfo: Object,
  },

  data: function () {

    return {
      loading: false,
      pName: Object,
      membership: '',
    }

  },

  computed: mapState({
    auth: state => state.auth,
    keyword: function () {
      let self = this
      let pStateUsers = this.$store.state.users
      var membership = this.event.event?.content.membership

      if (membership === 'join') return "joined in the chat"

      if (membership === 'invite') {
        let invitedUserID = this.event.event.state_key
        let invitedName = ''
         _.mapObject(pStateUsers,function (key, value){
             if('@' + key.id + ':matrix.pocketnet.app' === invitedUserID){
               return invitedName = key.name
             }
        })
        return "invited " + `${invitedName}` + " in the chat"
      }
     if (membership === 'ban') {
        let invitedUserID = this.event.event.state_key
        let invitedName = ''
         _.mapObject(pStateUsers,function (key, value){
             if('@' + key.id + ':matrix.pocketnet.app' === invitedUserID){
               return invitedName = key.name
             }
        })
        return "banned " + `${invitedName}` + " and room"
      }
      if(membership === 'leave'){

        let kickedUser = this.event.event.content.displayname
        let name = ''
        _.mapObject(pStateUsers,function (key, value){
          if(key.id === kickedUser){
            name = key.name
          }
        })
        return ' left' + ' the chat '
      }

      if(this.event.event.type === 'm.room.power_levels'){

        let newModer = Object.keys(this.event.event.content.users)[Object.keys(this.event.event.content.users).length-1]
        let name = ''

        _.mapObject(pStateUsers,function (key, value){
             if('@' + key.id + ':matrix.pocketnet.app' === newModer){
               // console.log(key, "leyleelelele")
               return name = key.name
             }
        })
        
        return 'made ' + `${name}` + ' a moderator'
      }

      if(this.event.event.type === 'm.room.name'){
        return 'edited chat name to ' + `"${this.event.event.content.name}"`
      }

    }
  }),

  methods: {},
}