<template>
  <div id="url">
    <div v-if="urltype!= 'impty'">
        <metaMessage
            v-if="!loading"

            ref="metamessage"

            :type="urltype"
            :name="meta['og:site_name']"
            :title="meta['og:title']"
            :description="meta['og:description']"
            :image="previewImageUrl"
            :video="meta['og:video:url']"
            :url="url"
            :h="meta['og:image:height']"
            :w="meta['og:image:width']"
            @updatedSize="updatedSize"
        />

        <div v-else>
            <div class="preloaderWrapper">
                <linepreloader />
            </div>
        </div>
    </div>
  
  </div>
</template>

<style scoped lang="sass">
    .preloaderWrapper
        padding : $r 0
        text-align: center
</style>

<script>
import { mapState } from 'vuex';
import metaMessage from "@/components/events/event/metaMessage/index.vue";
export default {
    name: 'eventsurl',
    props: {
        url : String,
        preview : Boolean,
        data : Object
    },
    components : {
        metaMessage
    },
    data : function(){
        return {
            meta : {},
            loading : false
        }
    },
    computed: mapState({
        auth : state => state.auth,
        previewImageUrl() {
            if (this.meta['og:image'])
                return this.core.mtrx.client.mxcUrlToHttp(this.meta['og:image']);
        },
        urltype : function(){

            if(!this.url) return 'empty'

            let url = new URL(this.url)

            if(url.host === 'pocketnet.app') {
                return 'pocketnet'
            }

            return 'custom'
        },

    }),

    mounted : function(){
        console.log('this.urltype', this.urltype, this.url)
        if (this.urltype == 'custom'){
            this.geturl()
        }
    },

    methods : {
        updatedSize: function(before) {
            this.$emit('updatedSize', before)
        },
        geturl : function(){

            this.loading = true

            this.core.mtrx.client.getUrlPreview(this.url, 0).then(response => {
                this.loading = false
                this.meta = response

            }).catch(error => {

                this.loading = false

                console.log(error)
            })

        }

    },
}
</script>