import {mapState} from 'vuex';
import actions from "@/components/events/event/actions/index.vue";
import filePreview from "@/components/events/previews/filePreview/index.vue";
import fileMessage from "@/components/events/event/fileMessage/index.vue";
import listPreview from "@/components/events/event/message/listPreview/index.vue";
import f from '@/application/functions'
import VuePictureSwipe from 'vue-picture-swipe';
import url from '@/components/events/event/url/index.vue'
import imagesLoaded from 'vue-images-loaded'

export default {
  name: 'eventsMessage',
  props: {
    origin: Object,
    event: Object,
    preview: Boolean,
    userinfo: Object,
    readed: Boolean,
    baseImg: {
      type: String,
      default: 'empty'
    },
    filePreview: Object,
    imgEvent: {},
    add_image: Function,
    goToGallery: Function,
    clientWidth: Number,
    chat: Object,
    encrypted: false
  },
  directives: {
    imagesLoaded
  },

  data: function () {

    return {
      loading: false,
      msgActions: false,
      isActive: false,
      menuItems: [
        {
          click: "reply",
          title: "Reply",
          icon: "fas fa-reply"
        },
        {
          click: "copy",
          title: "Copy",
          icon: "far fa-copy"
        },
        {
          click: "delete",
          title: "Delete",
          icon: "far fa-trash-alt"
        }
      ]
    }

  },
  components: {
    actions,
    filePreview,
    fileMessage,
    VuePictureSwipe,
    listPreview,
    url
  },
  computed: mapState({
    auth: state => state.auth,
    my: function () {
      return this.userinfo.id === this.core.user.userinfo?.id
    },
    imageWidth: function () {
      return this.clientWidth < 650 ? this.imageFactor * this.initImageWidth : this.initImageWidth;
    },

    imageHeight: function () {

      if (this.content && this.content.info) {

        const {h, w} = this.content.info;
        const ratio = w / this.imageWidth;

        return h / ratio || 300;

      }

      return;

    },

    content: function () {
      return this.origin.getClearContent() || this.origin.event.content
    },

    textWithoutLinks: function () {
      if (this.urlpreview) {
        return this.$f.trim(this.content.body.replace(this.urlpreview, ''))
      }

      return this.content.body
    },

    imageUrl: function () {
      return this.content && this.content.url;
    },

    file: function () {
      if (this.content.msgtype === 'm.file') {
        return JSON.parse(this.content.body)
      }
    },
    urlpreview: function () {
      if (this.content.msgtype !== 'm.file') {
        if (this.content.body) return f.getUrl(this.content.body) || ""
      }
    }

  }),

  created() {

  },
  mounted() {


  },
  methods: {
    imagesLoaded: function () {
      this.updatedSize()
    },
    updatedSize: function (before) {
      this.$emit('updatedSize', before)
    },
    menureply: function () {
      return Promise.resolve()
    },
    menucopy: function () {
      this.$f.copytext(this.content.body)

      return Promise.resolve()
    },
    menudelete: function () {

      this.$emit('remove')

      return this.core.mtrx.client.redactEvent(this.chat.roomId, this.origin.event.event_id, null, {
        reason: "messagedeleting",
      })

    },
    menuItemClickHandler: function (item, d, p) {

      p.hidePopup()

      this['menu' + item.click]().then(r => {

      }).catch(e => {
        p.showPopup()
      })

    },
    parser(event) {
      return JSON.parse(event)['og:title']
    },
    openImage(img) {
      this.$emit('openImg', img)
    },

    clickedOutside() {
      this.msgActions = false
      this.isActive = false
    },

    format_date(value) {
      if (value) {
        return new Date(value).toLocaleTimeString([], {hour: '2-digit', minute: "2-digit", hour12: false})
      }
    },

    handleMsgActionsList() {
      this.msgActions = !this.msgActions
      this.isActive = !this.isActive
    },


    openImageGallery(msgEvent) {
      this.$emit('openGalleryImg', msgEvent)
    },

    textDonation: function (withTx = false) {
      this.readed = false;
      var from = 'Somebody', msg = '';
      try {
        from = this.$f.deep(this, '$store.state.users.' + this.content.from).name;
      } catch (err) {
      }
      var to = 'somebody';
      try {
        to = this.$f.deep(this, '$store.state.users.' + this.content.to).name;
      } catch (err) {
      }
      msg += from + ' sent ' + this.content.amount + ' PKOIN to ' + to;
      if (withTx)
        msg += '<br/><a href="https://pocketnet.app/blockexplorer/transaction/' + this.content.txId + '" target="_blank">View transaction</a>';
      return msg;
    },

  }
}