<template>

  <div ref="metaMsg" class='metaMsg'>

    <div class="pocketnet_iframe" v-if="iframe" ref="iframe">
      
    </div>

    <div class="customURL" v-else>

      <div class="header">  

        <div v-if="image">
          <bgimage :src="image" />
        </div>

        <div class="header-text">
          <span class="title">{{ title }}</span>
        </div>
      </div>

      <div class="description">
        <span>{{ description }}</span>
      </div>
      <div class="link">
        <a :href="url"><span>{{url}}</span></a>
      </div>
      <div 
          v-if="!video" 
          class="image"
        >
      <img :src="image" alt="meta Image" v-if="image" v-images-loaded:on.loaded="imagesLoaded">
    </div>
      <Player v-if="video" :video="video_url" :autoplay="true" />
    </div>
  </div>
</template>
<script>
import Player from '@/components/chat/player/Player.vue'
import Widgets from '@/application/utils/widgets.js'


import imagesLoaded from 'vue-images-loaded'

import f from '@/application/functions'
import { createLogger } from 'node_modules/vuex/types'
// import Plyr from 'plyr'

export default {
  components: {
    Player
  },
  directives: {
    imagesLoaded
  },
  props: {
    title: String,
    msg: String,
    name: String,
    description: String,
    image: String,
    video: String,
    url: String,
    h: Number, 
    w: Number,
    initImageWidth: Number,
    imageFactor: Number,
    clientWidth: Number,
    post: String,
    type : String,
  },

  data: () => {
    return {
      link: '',
      txt: String,
      iframe: null
    }
  },
  computed: {

    imageWidth(){

      return this.clientWidth < 650 ? this.imageFactor * this.initImageWidth : this.initImageWidth;
    },
      getUrl : function () {
        if(this.metaUrl) return new URL(this.metaUrl).hostname === 'pocketnet.app'
    },

    imageHeight(){

      const ratio = this.w / this.imageWidth;

      return this.h / ratio || 300;
    }
  },

  beforeMount() {
    if(this.video) this.video_url = this.video

  },
  mounted() {
    

    if(this.type === 'pocketnet') {

      this.iframe = true
      let widget = new Widgets()

      f.pretry(() => {

        return this.$refs.iframe

      }).then(() => {

        widget.makefromurl(this.$refs.iframe, this.url, (before) => {
          this.updatedSize(before)
        })

        
      })
    
    }
    else
    {
      this.updatedSize()
    }
  },
  methods: {
    imagesLoaded : function(){
      this.updatedSize()
    },
    updatedSize: function(before) {
        this.$emit('updatedSize', before)
    },
  },

}
</script>

<style lang="less" src="@/components/events/event/metaMessage/exported.less"></style>

<style lang="sass" scoped>

.metaMsg
  width: 100%
  padding: 0
  height: auto
  overflow: hidden

  .customURL
    background: transparentize($color-text-mlight, 0.9)
    border-radius: 5px

  .link
    padding : $r
    padding-top : 0
    span
      text-decoration: underline
      font-size: 0.8em

  .header
    display: flex

    .header-text
      flex-direction: column
      padding : $r

      .title
        margin: 0
        
    .pocketnet
      font-size: 11px
      font-weight: 500
      color: gray

  .bgimage
    width : 50px
    height: 50px
    border-radius: 5px
    border-bottom-left-radius: 0
    border-top-right-radius: 0
  .textMsg
    word-break: break-all
    font-size: 0.8em
    cursor: pointer
    &:hover
     a
       display: block
       width: 100%
       height: 100%
       color: #62bcf9

    a
      font-weight: bold
      color: #0059ff

  .title
    font-weight: bold
    display: block
    font-size: 0.9em
    margin: 0.4em 0
    span
      display: block

  .description
    padding : $r
    
    display: flex
    justify-content: flex-start

    span
      font-size: 0.8em

  .image
    margin: 0.2em 0

    img
      display: block
      object-fit: contain
      border-radius: .5em
      width: 100%
      object-fit: cover
  .video
    margin: .5em 0
    iframe
      border: 0
      border-radius: .5em

</style>