<template>

  <div class="wrapper table">

      
    
      <div class="filecontent">
        <div class="name">
          <span>{{ file.name }}</span>
        </div>

        <div class="size">
          <span>{{humanReadableSize(file.size) }}</span>
        </div>

        
        <div class="download">
          <a :href="file.url" :download="file.name" target="_external" class="fileMessage">
            <button class="button small rounded">Download </button>
          </a>
        </div>
        
      </div>

      <div class="icon">
        <a :href="file.url" :download="file.name" target="_external" class="fileMessage">
          <i class="fas fa-file-download"></i>
        </a>
      </div>

  </div>
</template>
<script>
import clipLoader from "vue-spinner/src/ClipLoader.vue"
import f from "@/application/functions";

export default {
  props: {
    preview: Object,
    file:Object,
  },
  computed : {
    getExtension: function (file) {
      var name = file.name.split('.');
      var ext = name[name.length - 1].toLowerCase();

      return ext;
    },
  },
  methods: {
     humanReadableSize(data) {
      return f.formatBytes(data)
    },
  }
}
</script>
